import http from '@/plugins/axios'

export function wx_config(params) {
    /**
     * @description 
     * @retrun (promise)
     * 
     * @params (object):
     *  @url (string): URL#
     */
    return http({
        url: 'v1/wx/config',
        method: 'get',
        params
    })
}

export function lgoin (data) {
    /**
     * @description 
     * @return (promise)
     * 
     * @data (object):
     *  @code (string): 
     *  @contact_phone (string): 
     */
    return http({
        url: 'v1/merchants/login',
        method: 'post',
        data
    })
}

export function verify_code(params) {
    /**
     * @description 
     * @return (promise)
     * 
     * @params (object):
     *  @mobile (string): 
     */
    return http({
        url: 'v1/verify_code',
        method: 'get',
        params
    })
}

export function get_merchant_info(params) {
    /**
     * @description 
     * @return (promise)
     * 
     * @parmas (object): 
     *  @merchant_id (string): ID,
     */
    return http({
        url: 'v1/merchants/detail',
        method: 'get',
        params
    })
}

export function writeoff_info (params) {
    /**
     * @description 
     * @return (promise)
     * 
     * @params (object):
     *  @customer_id (number): ID
     */
    return http({
        url: 'v1/merchants/writeoff',
        method: 'get',
        params
    })
}

export function writeoff(data) {
    /**
     * @description 
     * @return (promise)
     * @data (object):
     *  @customer_id (string): id
     *  @num (number): 
     */
    return http({
        url: 'v1/merchants/writeoff',
        method: 'post',
        data
    })
}


