import Vue from 'vue'
const wx = require('weixin-js-sdk')

const jsApiList = [
  'scanQRCode'
]

export function wxAuthority(options) {
    wx.config({
      debug: false,
      appId: options.appid, // 
      timestamp: options.timestamp, // 
      nonceStr: options.noncestr, // 
      signature: options.signature, // 
      jsApiList // JS
    })
  
    wx.ready(() => {
      wx.checkJsApi({
        jsApiList,
        success(res) {
          console.log(res)
        }
      })
    })
    wx.error(err => {
      console.log(err.message)
      // Vue.$vux.toast.show({
      //   text: `${err}`,
      //   type: 'text'
      // })
    })
  }

  export function scanQRCode() {
    wx.scanQRCode({
      needResult: 0, // 01
      scanType: ["qrCode","barCode"], // 
      success(res) {
          console.log(res)
      }
    });
  }




