import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import WriteOff from '@/views/WriteOff'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'home',
		component: Home
	},
	{
		path: '/login',
		name: 'login',
		component: () => import('@/views/Login')
	},
	{
		path: '/writeoff',
		name: 'write_off',
		component: WriteOff
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router



